var _ = require('lodash');
var xhr = require('xhr');
var java = require('./lib/java')



var defaultOptions = {
    xhrPath: '/api'
};

module.exports = function(options) {
    options = _.extend({}, defaultOptions, options);

    var serverPigeon = function(url, method, params, callback) {
        callback(null, {
            territoryName: '123'
        })      
        // var pigeonClient = require('node-pigeon-client');
        // // node
        // pigeonClient.getService(url, function(err, service) {   
        //     if (err) {
        //         console.log(err);
        //         return;
        //     }

        //     var args = params.concat([callback]);

        //     service[method].apply(null, args)
        // })
    }

    var pigeon = typeof window  === 'undefined' ?  serverPigeon: function(url, method, params, callback) {
       
        // browser
        xhr({
            url: options.xhrPath,
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                method: method,
                url: url,
                params: params
            })
        }, function(err, resp, body) {
            if (err) {
                console.log(err);
                return
            }
            callback(null, JSON.parse(body));
        })        

    }

    var getMiddleware = function() {
        return function(req, res, next) {
            var data = req.body;
            serverPigeon(data.url, data.method, data.params, function(err, result) {
                if (err) {
                    console.log(err);
                    return;
                }
                res.json(result);
            })
        }
    }

    return {
        name: 'PegionPlugin',

        plugContext: function(contextOptions) {
            return {
                plugActionContext: function(actionContext) {                    
                    actionContext.pigeon = pigeon;
                    actionContext.pigeon.type = java;
                }
            }
        },

        getMiddleware: getMiddleware,

        getXhrPath: function getXhrPath() {
            return options.xhrPath;
        }
    }
}